/* Reset de estilos para todos los elementos */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

img {
  -webkit-user-drag: none; /* para Safari */
  -khtml-user-drag: none; /* para Konqueror */
  -moz-user-drag: none; /* para Firefox */
  -o-user-drag: none; /* para Opera */
}

/* Estilos generales para la página */
body {
  font-family: 'Sriracha-Regular';
  background-color: #0d0d0d;
  color: #fff;
}

/* Estilos para la barra de navegación */
nav {
  font-family: 'Sriracha-Regular';
  border-bottom: 0.5em solid var(--color-primario);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: .5em 1em;
  border-radius: 0.5em;
}

nav a {
  color: var(--color-texto);
  text-decoration: none;
  font-size: 1.2em;
  font-weight: bold;
  transition: all 0.3s ease;
}

nav a:hover {
  transform: scale(1.1);
  color: var(--color-secundario);
}

.connect-wallet {
  margin-left: auto; /* Posiciona el botón a la derecha */
}

.connect-wallet button {
  font-family: 'Sriracha-Regular';
  background: linear-gradient(to right, #a8b8fd, #816bfc, #5654fc, #285fb1);
  color: rgb(255, 253, 254);
  height: 3.4em;
  border: none;
  border-radius: 0.75em; /* 12px */
  padding: 0.625em 2.5em; /* 10px 40px */
  font-size: 1em; /* 16px */
  transition: all 0.3s ease;
  margin: 0.3em 0.2em 0.2em 0.2em;
  background-size: 400% 400%;
  animation: gradient 8s ease infinite;
}

.connect-wallet button:hover {
  background-position: right center;
  cursor: pointer;
}

.connect-wallet button:focus {
  outline: none;
}

@keyframes gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

/* Estilos para el pie de página */
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed; /* Posicionamiento fijo */
  bottom: 0; /* Coloca el footer al fondo */
  width: 100%; /* Opcional: ancho completo */
  color: #fff; /* Color del texto */
  margin-top: 1em;
}


.twitter img,
.contrato img {
  max-width: 2.5em;
  margin: 0 0.925em;
}

.content {
  background-color: #132e49; /* Color de fondo */
  padding: 1.25em; /* Espacio interno */
  border-radius: 0.625em; /* Borde redondeado */
  box-shadow: 0 0 0.625em rgba(240, 73, 73, 0.3); /* Sombra */
  margin: 1.25em auto; /* Margen y centrado horizontal */
  max-width: 37.5em; /* Ancho máximo */
  animation: fadeIn 1s ease-in-out; /* Animación de entrada */
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.text-container {
  color: #fff; /* Color del texto */
}

.form-container {
  margin-bottom: 1.25em;
}

.letrainput {
  font-size: 1.1em; /* Tamaño de la fuente */
  margin: 0.4em;
}
.calculo {
  display: flex;
  align-items: center;
  font-size: 1em; /* Tamaño de fuente en relación con el tamaño de fuente del elemento padre */
  margin: 0.3em;
  text-align: center;
}

.calculo img {
  width: 1.25em; /* Ancho de la imagen */
  height: 1.25em; /* Altura de la imagen */
  margin: 0 0.3125em; /* Espacio entre la imagen y el texto */
}

.icono-moneda {
  width: 3.125em; /* Ancho de la imagen */
  height: 3.125em; /* Altura de la imagen */
  margin-bottom: 0.625em; /* Margen inferior */
}

.contenedorinput {
  margin-bottom: 0.625em;
}

.inputValueate {
  display: block;
  margin-bottom: 0.3125em;
  font-size: 0.9em;
}

.input {
  width: calc(100% - 0.625em); /* Ancho del input */
  border: 0.2em solid rgb(250, 59, 59);
background: #0d0d0d;
  padding: 0.8em;
  border-radius: 1em;
  color: aliceblue;
  transition: border-color 0.3s ease;
  -webkit-appearance: none; /* Para Safari y Chrome */
  -moz-appearance: none; /* Para Firefox */
  appearance: none; /* Para otros navegadores */
}


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Estilos cuando el input obtiene el foco */
.input:focus {
  border-color: #cee4fc;
}

/* Animación para resaltar los elementos */
@keyframes highlight {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.calculate button,
.staking button {
  background-color: #3498db; /* Color de fondo del botón */
  color: #fff; /* Color del texto del botón */
  border: none;
  padding: 0.625em 1.25em; /* Espacio interno */
  border-radius: 0.3125em; /* Borde redondeado */
  cursor: pointer;
  transition: background-color 0.3s; /* Transición suave */
}

.calculate button:hover,
.staking button:hover {
  background-color: #2980b9; /* Color de fondo del botón al pasar el ratón por encima */
}
.j{
  margin: 0.3em;
}